import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'
export default {
	components: {
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			state:'',
			msg:'',
			uid: "",
			curr_worktype_name: '',
			curr_worktype_bh: '',
			chuanghao: '',
			huohao: '',
			worktype: "",
			sl: '',
			price: '',
			je: '',
			info: '',
			list: [],
			userid: '',
			color: '',
			chima: '',
			ganghao: '',
			dk: ''
		}
	},
	onBackPress(options) {
		if (options.from === 'navigateBack') {
			uni.switchTab({
				url: '/pages/index/index'
			});
			return true;
		}
		return false;
	},
	onLoad() {
		this.dk = this.websiteUrl,
			uni.getStorage({
				key: 'fwqsj',
				success: res => {
					this.dk = res.data
				}
			})
		uni.getStorage({
			key: 'loginData',
			success: res => {
				this.uid = res.data.uid,
					this.curr_worktype_bh = res.data.curr_worktype_bh
			}
		})


	},
	methods: {
		focus(){
			this.userid = '';
		},
		getData() {
			uni.request({
				url: `https://${this.dk}/downloadDeviceCMDs.aspx`,
				method: 'GET',
				data: {
					uid: this.uid,
					worktype: this.curr_worktype_bh,
					Info: this.userid,
					Inputsl: 0
				},
				complete: res => {
					if (res.statusCode == 200) {
						this.state = res.data.state;
						this.huohao = res.data.huohao;
						this.color = res.data.color;
						this.chima = res.data.chima;
						this.chuanghao = res.data.chuanghao;
						this.sl = res.data.sl;
						this.msg = res.data.tsxx;
						uni.request({
							url: `https://${this.dk}/getSearchWorktype.aspx`,
							method: 'GET',
							data: {
								userid: this.userid
							},
							success: res => {
								if (res.statusCode == 200) {
									//console.log(JSON.stringify(res));
									this.list = res.data;
								}
							}
						});

					} else {
						this.list = [];
						this.huohao = '';
						this.color = '';
						this.chima = '';
						this.ganghao = '';
						uni.showToast({
							position: 'top',
							title: '请求数据失败，请输入正确的条码',
							icon: 'none',
							duration: 2000
						})
					}
				}
			})
		}
	}
}
